<template>
    <el-row :gutter="20" class="main-row">
      <!-- 左侧的列 -->
      <el-col :span="4" class="leftside-col">
          <div  v-for="item in areaList">
            <el-card shadow="always" class="leftclass">
              <LeftSideCard :area_id="item.area" :area_value="item.area_value" :area_unit="item.area_unit"
                :monitor_value="item.monitor_value" :warning_value="item.warning_value"></LeftSideCard>
            </el-card>
          </div>
        
      </el-col>

      <el-col :span="20" class="rightside-col">
        <el-row :gutter="20" style="height:15%" type="flex" align="middle">
          <el-col :span="12">
            <div style="text-align:center">
              <img :src="require('@/assets/trackingimg/BatchParameter1.png')" alt="picture1" width="70%">
            </div>
          </el-col>
          <el-col :span="3">
            <div style="text-align:center;height:100px;">
              <img :src="require('@/assets/trackingimg/BatchParameter2.png')" alt="picture2" width="100%" height="100%">
