          </el-col>
          <el-col :span="3">
            <div style="text-align:center;height:100px;">
              <img :src="require('@/assets/trackingimg/BatchParameter3.png')" alt="picture3" width="100%" height="100%">
            </div>
          </el-col>
          <el-col :span="3">
            <div style="text-align:center;height:100px;">
              <img :src="require('@/assets/trackingimg/BatchParameter4.png')" alt="picture4" width="100%" height="100%">
            </div>
          </el-col>
          <el-col :span="3">
            <div style="text-align:center;height:100px;">
              <img :src="require('@/assets/trackingimg/BatchParameter5.png')" alt="picture5" width="100%" height="100%">
            </div>
          </el-col>
        </el-row>
        <!-- 表1 -->
        <el-row style="height:41%;margin-top:10px;" :gutter="20" type="flex" align="middle">
          <el-col :span="12">
            <el-card shadow="always">
              <BatchTable1></BatchTable1>
            </el-card>
          </el-col>
        <!-- 表2 -->
          <el-col :span="12">
            <el-card shadow="always">
              <BatchTable2></BatchTable2>
            </el-card>
          </el-col>
        </el-row>

        <el-row style="height:42%;margin-top:10px;" :gutter="20" type="flex" align="middle">
        <!-- 表3 -->
          <el-col :span="12">
            <el-card shadow="always">
              <BatchTable3></BatchTable3>
            </el-card>
          </el-col>
          <!-- 右下角进度条 -->
