            monitor_value: "15",
            warning_value: "2"
          },
          {
            area: "BTS",
            area_value: "23424",
            area_unit: "T/h",
            monitor_value: "15",
            warning_value: "2"
          },
          {
            area: "BTS",
            area_value: "23424",
            area_unit: "T/h",
            monitor_value: "15",
            warning_value: "2"
          },
