<style scoped>
  .main-row {
    height: 99%;
  }

  .leftside-col {
    height: 100%;
    overflow: auto;
  }

  /* .leftside-col:hover {
    overflow-y: auto;
  } */
  .leftside-col::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 3px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 4px;
  }

  .leftside-col::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
  }

  .leftside-col::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 0;
    background: rgba(0, 0, 0, 0.1);
  }

  .leftclass {
    /* height: 100px; */
    /* background-color: white; */
    margin-bottom: 10px;
    /* border-radius: 5px; */
  }

  .rightside-col {
    height: 100%;
    /* background-color: red; */
  }

  .right-buttom-col {
    border-radius: 4px;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;

  }

  /* .right-buttom-col:hover {
    overflow-y: auto;
  } */
